#game{
    background-color: #222222;
    background: repeating-linear-gradient(
        3deg,
        #2b2b2b 0%,
        #2b2b2b 10%,
        #222222 0%,
        #222222 50%
        )
        0 /
        15px
        15px;
        
    width:100%;
    height: 100vh;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal-main{
    position: fixed;
    background: #000;
    min-width: 300px;
    max-width: 500px;
    width: 100%;
    color: #fff;
    text-align: center;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.hide{
    display: none;
}

h1{
    width: 100%;
    font-size: 40px;
    margin: 0.5em 0;
}

h2{
    width: 100%;
    font-size: 20px;
    margin: 0.5em 0;
}

.modal-btn{
    margin: 0.5em;
    font-size: 2em;
    border: none;
}

.modal-btn:hover{
    cursor: pointer;
    opacity: 0.8;
}