body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#game{
    background-color: #222222;
    background: repeating-linear-gradient(
        3deg,
        #2b2b2b 0%,
        #2b2b2b 10%,
        #222222 0%,
        #222222 50%
        )
        0 /
        15px
        15px;
        
    width:100%;
    height: 100vh;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal-main{
    position: fixed;
    background: #000;
    min-width: 300px;
    max-width: 500px;
    width: 100%;
    color: #fff;
    text-align: center;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.hide{
    display: none;
}

h1{
    width: 100%;
    font-size: 40px;
    margin: 0.5em 0;
}

h2{
    width: 100%;
    font-size: 20px;
    margin: 0.5em 0;
}

.modal-btn{
    margin: 0.5em;
    font-size: 2em;
    border: none;
}

.modal-btn:hover{
    cursor: pointer;
    opacity: 0.8;
}
hr{
    background-color: #fff;
    height: 2px;
}

ol{
    padding-right: 40px;
}

#chalkBoard{
    min-width: 330px;
    width: 100%;
    max-width: 1000px;
    height: 500px;
    border: 6px solid #c19b6c;
    background-color: #3b653d;
    display: flex;
    position: relative;
    flex-wrap: wrap;
    justify-content: space-around;
}

#rules-container{
    min-width: 15%;
    max-width: 33%;
    padding-right: 1em;
}

#eraser{
    position: absolute;
    width: 75px;
    height: 15px;
    bottom: 0;
    right: 5%;
}

#eraser-top{
    background-color: #000;
    position: relative;
    width: 100%;
    height: 5px;
    padding: 0 5px;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}

#eraser-bottom{
    background-color: gray;
    position: relative;
    width: 100%;
    height: 10px;
    margin: 0 5px;
}

#score-area{
    height: 100px;
    width: 100%;
    margin-left: 0.5em;
    margin-right: 0.5em;
    border-top: 1px solid #fff;
    align-self: flex-end;
    display: flex;
    justify-content: space-between;
}

#player-one-score-container,
#player-two-score-container{
    display: flex;
    flex-wrap: wrap;
}

#player-one-score-container h5,
#player-two-score-container h5{
    width: 100%;
    font-size: 20px;
    margin: 0;
    margin-bottom: 0.25em;
    padding: 0;
    border-bottom: 2px solid #fff;
}

#player-one-score-container span,
#player-two-score-container span{
    font-size: 15px;
    margin: 0 0.5em;
    display: block;
    width: 100%;
}

#swap-btn-container{
    position: absolute;
    bottom: 0.5em;
}

#swap-btn{
    font-size: 16px;
    font-weight: bold;
    background: rgba(0,0,0,0);
    color: #fff;
    padding: 0.5em 1em;
    border: 1px solid #fff;
}

#swap-btn:hover{
    cursor: pointer;
    opacity: 0.8;
}

@media only screen and (max-width: 470px) {
    #play-area{
        width: 50%;
    }
}

#play-area{
    width: 60%;
    max-width: 500px;
    height: 20px;
    margin-top: 2em;
    display:flex;
    flex-wrap: wrap;
}

.row{
    width: 100%;
    display: flex;
}
#top-row{
    border-bottom: 2px solid #fff;
    height: 100px;
}

#mid-row{
    border-bottom: 2px solid #fff;
    height: 100px;
}

#bot-row{
    height: 100px;
}

.col{
    width: 33%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.col:hover{
    cursor: pointer;
}

.left-col{
    border-right: 2px solid #fff;
}

.mid-col{
    border-right: 2px solid #fff;
}

span{
    font-size: 80px;
    font-weight: bold;
}
